@font-face{
  font-family:'DNFBitBitv2';
  font-style:normal;
  font-weight:400;
  src: url('//cdn.df.nexon.com/img/common/font/DNFBitBitv2.otf')format('opentype')
};

input::placeholder {
  color: black;
  font-family:'DNFBitBitv2';
}

.Test1{
  width: 100vw;
  height: 100vh;
  background-image: url(./component/images/T1_background01.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
  align-items: stretch;
  justify-content: space-around;
}

.Test1 div{
  color: pink;
  font-family:'DNFBitBitv2';
}

.Test1Q1{
  width: 100vw;
  height: 100vh;
  background-image: url(./component/images/T1_background01.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
  align-items: stretch;
  justify-content: space-around;
  font-family:'DNFBitBitv2';
}

.Test1Q1 .item:nth-child(1) {
  background-color: white;
  line-height: 100px;
  flex: 0 1 100px;
}
.Test1Q1 .item:nth-child(2) {
  color : white;
  -webkit-text-stroke: 0.5px black;
  margin-left: 100px;
  margin-right: 100px;
  font-size: 28px;
  flex: 0 1 82px;
  line-height: 60px;
}
.Test1Q1 .item:nth-child(3) {
  background-image: url(./component/images/name.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100%;
  margin-left: 15%;
  margin-right: 15%;
  flex: 0 1 100px;
  position: relative;
}

.Test1Q1 .item:nth-child(3) input{
  width: 70%;
  position: absolute;
  left: 20%;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  line-height: 50px;
  border-right: 0px;
  border-top: 0px;
  border-left: 0px;
  border-bottom: 0px;
}

.Test1Q1 .item:nth-child(4) {
  background-color: blue;
  flex: 0 1 100px;
  margin-left: 15%;
  margin-right: 15%;
}
.Test1Q1 .item:nth-child(5) {
  flex: 0 1 100px;
  line-height: 100px;
  margin-left: 15%;
  margin-right: 15%;
  background-image: url(./component/images/T1_button.png);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.home{
  background-color: orange;
}

.questionList{
  flex-direction: column;
  height: 100%;
  overflow: scroll;
  scrollbar-width: none;
}

.question{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  text-align: center;
}

.answerBox{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  text-align: center;
  box-shadow: 2px 2px 0px #ededed;
  border: 1px solid #ededed;
  margin: 0px 20px 15px 20px;
  border-radius: 10px;
}
